/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import T from 'prop-types';
import Heading from '@splunk/react-ui/Heading';
import Markdown from '@splunk/react-ui/Markdown';
import JSONTree from '@splunk/react-ui/JSONTree';
import styled from 'styled-components';

const EventTester = styled.div`
    margin-top: 50px;
`;

const Event = styled.div`
    margin-top: 20px;
`;
const EventName = styled.div`
    color: #5ca300;
    padding-bottom: 5px;
`;

const EventItem = ({ name, desc }) => (
    <Event>
        <EventName>{name}</EventName>
        <Markdown style={{ marginLeft: 40 }} text={desc} />
    </Event>
);

EventItem.propTypes = {
    name: T.string,
    desc: T.string,
};

const EventsDesc = ({ events }) => (
    <div>
        {events.length === 0 ? <Heading level={4}>No Events</Heading> : <Heading level={4}>Events</Heading>}
        {events.map(e => (
            <EventItem key={e.name} {...e} />
        ))}
    </div>
);

EventsDesc.propTypes = {
    events: T.array.isRequired,
};

const VisualiationExample = ({
    event,
    Visualization,
    vizProps,
    onPointClick,
    onPointMouseOver,
    onPointMouseOut,
    onLegendClick,
    onRangeSelect,
}) => {
    const eventToDisplay = {
        ...event,
    };
    if (eventToDisplay.originalEvent != null) {
        eventToDisplay.originalEvent = '...Original Event instance...';
    }
    return (
        <div>
            <EventTester>
                <Heading level={4}>Test Visualization</Heading>
                <Visualization
                    {...vizProps}
                    onPointClick={onPointClick}
                    onPointMouseOver={onPointMouseOver}
                    onPointMouseOut={onPointMouseOut}
                    onLegendClick={onLegendClick}
                    onRangeSelect={onRangeSelect}
                />
            </EventTester>
            <Heading level={4}>Event Monitor</Heading>
            <div>
                <JSONTree json={eventToDisplay} expandChildren />
            </div>
        </div>
    );
};

VisualiationExample.propTypes = {
    event: T.object,
    Visualization: T.any,
    vizProps: T.object,
};

class EventsDoc extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            e: null,
        };
    }

    handleEvent = e => {
        this.setState({
            e,
        });
    };

    render() {
        const { Visualization, events, vizProps } = this.props;
        const { e } = this.state;
        return (
            <div>
                <EventsDesc events={events} />
                {Visualization && (
                    <VisualiationExample
                        event={e}
                        Visualization={Visualization}
                        vizProps={vizProps}
                        onPointClick={this.handleEvent}
                        onPointMouseOver={this.handleEvent}
                        onPointMouseOut={this.handleEvent}
                        onLegendClick={this.handleEvent}
                        onRangeSelect={this.handleEvent}
                    />
                )}
            </div>
        );
    }
}

EventsDoc.propTypes = {
    events: T.array,
    Visualization: T.any,
    vizProps: T.object,
};

export default EventsDoc;
